.card{
    margin: 0; 
    padding: 0; 
    display: flex; 
    justify-content: center; 
    align-items: center; 
    height: calc(100svh-80px);
}
.addcard{
    border : 1px solid white;
    width: 30rem;
    display: flex;
    justify-content: center;
    margin:15px;
    border-radius: 10px;
    padding: 5px;
    gap:3px;
}